import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home'
import Tempo from '@/components/Tempo'
import Setting from '@/components/Setting'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/tempo',
      component: Tempo
    },
    {
      path: '/setting',
      component: Setting
    }
  ]
})



